.hero {
  padding-top: 64px;
}

.heroGrid {
  display: grid;
  grid-template-columns: 1.2fr 0.8fr;
  gap: 36px;
  align-items: start;
}

.heroLeft {
  display: flex;
  flex-direction: column;
}

.heroTitleBlock {
  display: flex;
  flex-direction: column;
  gap: 0;
}

.kicker {
  font-size: 12px;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: var(--muted);
  margin-bottom: 18px;
}

.h1 {
  font-size: clamp(46px, 6vw, 84px);
  line-height: 0.95;
  letter-spacing: -0.04em;
  margin: 0;
}

.sub {
  margin-top: 18px;
  max-width: 680px;
  font-size: 18px;
  line-height: 1.6;
  color: var(--muted);
}

.heroSub {
  /* keeping this as a hook in case you want hero-specific tweaks */
}

.heroButtons {
  margin-top: 28px;
}

.buttons {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
}

/* button style scoped ONLY to hero */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;

  padding: 10px 14px;
  border-radius: 999px;
  border: 1px solid var(--border);
  background: white;

  font-size: 14px;
  font-weight: 600;

  cursor: pointer;
  transition: transform 140ms ease, opacity 140ms ease,
    background 140ms ease;
}

.btn:hover {
  transform: translateY(-1px);
  opacity: 0.92;
  background: rgba(0, 0, 0, 0.02);
}

.btn:active {
  transform: translateY(0px);
  opacity: 0.85;
}

/* right photo */
.heroPhotoWrap {
  display: flex;
  justify-content: flex-end;
}

.heroPhoto {
  width: 100%;
  max-width: 360px;
  border-radius: 22px;
  overflow: hidden;
  border: 1px solid var(--border);
  background: #fff;
}

.heroPhoto img {
  width: 100%;
  height: auto;
  display: block;
}

/* responsive */
@media (max-width: 900px) {
  .hero {
    padding-top: 40px;
  }

  .heroGrid {
    grid-template-columns: 1fr;
    gap: 22px;
  }

  .heroPhotoWrap {
    justify-content: flex-start;
    margin-top: 18px;
  }

  .heroPhoto {
    max-width: 320px;
  }

  .h1 {
    font-size: clamp(38px, 9vw, 64px);
  }

  .sub {
    font-size: 16px;
  }
}
